@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
    font-family: "Roboto";
    margin: 0;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0px;
    margin-bottom: 0.35rem;
}

h1 {
    font-weight: 300;
    font-size: 6rem;
    line-height: 1.167;
}

h2 {
    font-weight: 300;
    font-size: 3.75rem;
    line-height: 1.2;
}

h3 {
    font-weight: 400;
    font-size: 3rem;
    line-height: 1.167;
}

h4 {
    font-weight: 400;
    font-size: 2.125rem;
    line-height: 1.235;
}

h5 {
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 1.334;
}

h6 {
    font-weight: 500;
    font-size: 1.25rem;
    line-height: 1.6;
}
.Card-root {
    border-radius: 8px;
}

.CardHeader-root {
    display: flex;
    color: white;
    padding: 16px;
    align-items: center;
    background-color: cornflowerblue;
    border-radius: 8px 8px 0 0;
}

.CardHeader-title {
    flex-grow: 1;
    display: flex;
    align-items: center;
}

.CardHeader-title * {
    margin-bottom: 0;
}

.CardContent-root {
    padding: 16px;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 0 0 8px 8px;
}

.CloseButton-root {
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 1;
    background-color: transparent;
    color: inherit;
    border: none;
    cursor: pointer;
}

.CloseButton-root:focus {
    outline: none;
}

.CloseButton-root:hover {
    color: lightgray;
}

.CityForecast {
   border: 1px solid grey; 
   border-radius: 8px;
}

.CityForecast-icon {
    height: 40px;
}

.CityForecast-content {
    display: flex;
}

.CityForecast-container {
    display: flex;
}

.CityForecast-grid {
    display: grid;
    grid-template-columns: auto -webkit-max-content;
    grid-template-columns: auto max-content;
}

.CityForecast-grid :nth-child(odd) {
    margin-right: 24px;
}

.CityList {
}

.CityList > .container {
    width: 100%;
    max-width: 600px;
    display: block;
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    padding-left: 16px;
    padding-right: 16px;
}

@media (max-width: 599px) {
    .CityList > .container {
        max-width: 300px;
    }
}

@media (min-width: 960px) {
    .CityList > .container {
        max-width: 960px;
    }
}

@media (min-width: 1280px) {
    .CityList > .container {
        max-width: 1280px;
    }
}

@media (min-width: 1920px) {
    .CityList > .container {
        max-width: 1920px;
    }
}

.CityList .list {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 16px);
    box-sizing: border-box;
    margin: -8px;
}

.CityList .item {
    padding: 16px;
    width: 100%;
    box-sizing: border-box;
}

@media (min-width: 600px) {
    .CityList .item {
        width: 50%;
    }
}

@media (min-width: 960px) {
    .CityList .item {
        width: calc(100% / 3);
    }
}

@media (min-width: 1280px) {
    .CityList .item {
        width: 25%;
    }
}

@media (min-width: 1920px) {
    .CityList .item {
        width: calc(100% / 6);
    }
}

.App .background-image {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    min-height: 100vh;
    min-width: 100vw;
}
